<script>
import { mapGetters } from 'vuex';
export default {
    name: 'attendance',
    data() {
        return {
            attendClose: false,
            restClose: false,
            evectionClose: false,
            lateClose: false,
            leaveEarly: false,
            absenteeism: false,
            currentyear: '',
            detailList: {},
            monthPopup: false,
            selectMonth: this.$route.params.month,
            gender: ''
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    mounted() {
        let vm = this;
        document.setTitle('考勤信息详情');
        vm.currentyear = vm.formatDate(new Date(), 'yyyy');
        vm.gender = vm.userInfo.gender;
        // this.getPerInfo();
        vm.details();
        vm.getDayHoliday();
    },
    methods: {
        details() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/details', {
                'type': 'travelremark',
                'personID': this.$route.params.personId,
                'startTime': '2017-09-03',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                console.log(res);
                vm.detailList = res.jsonData;
            });
        },
        // 本日休假
        getDayHoliday() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/list', {
                'type': 'askforleave',
                'personID': vm.userInfo.userid,
                'startTime': '2018-02-03',
                'endTime': '2017-02-03',
                'hierachy': 'person',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                vm.dayHolidayList = res.jsonData;
            });
        }
        // getPerInfo: function () {
        //     let vm = this;
        //     vm.post(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryDepHistoryRecord.action', {
        //         'type': 'askforleave',
        //         'depName': '',
        //         'depID': '',
        //         'mouth': '6',
        //         'queryType': '',
        //         'personID': '13357758'
        //     }, function (res) {
        //         console.log(res);
        //     })
        // }
    }
}
</script>
