    margin-bottom: 0.03rem;
}

.accordion .mainContentFluid:last-child .topTitle {
    border-bottom: 1px solid #fff;
}

.daysNum .yd-flexbox .yd-flexbox-item {
    color: #999999;
}

.detailed .yd-flexbox {
    padding: 0 0.33rem;
    color: #333;
}

.detailed .yd-flexbox .yd-flexbox-item {
    color: #333;
}

.detailed .yd-flexbox:first-child,
.detailed .yd-flexbox:nth-child(3n) {
    background-color: #f8f8f8;
}

.gray {
    color: #999;
    padding-right: 0.3rem;
}

.black {
    color: #333;
    padding-right: 0.3rem;
}
